//        - comparison,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.comparison {
  display: flex;
  align-items: center;
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-size: 14px;
  font-weight: 300;
  color: $gray-dark;

  & svg {
    fill: #ccc;
  }
  &:hover {
    & svg {
      fill: $color-main;
    }
  }

  & span {
    margin-left: 15px;
  }
}
